
.game-touch-controls {
    aspect-ratio: 4 / 5;
    min-width: 100px;
    width: 20%;
    position: absolute;
    left: 10px;
    bottom: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.game-movement-touch-controls {
    flex-grow: 5;
    padding: 10px;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
    justify-content: stretch;
    align-items: stretch;
}

.game-w-button {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
}

.game-a-button {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
}

.game-s-button {
    grid-row: 3 / 4;
    grid-column: 2 / 3;
}

.game-d-button {
    grid-row: 2 / 3;
    grid-column: 3 / 4;
}

.game-orientation-controls {
    flex-grow: 2;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.game-orientation-touch-button {
    flex-grow: 1;
}

.game-touch-button {
    color: white;
    background-color: rgb(47, 79, 79);
    border-radius: 5px;
    border: 2px solid black;
    font-family: monospace;
	&:hover {
	    transition: background-color 0.2s transform 0.2s;
	    background-color: rgb(23, 39, 39);
	    transform: scale(1.05);
	}

	&:active {
	    transition: background-color 0.1s color 0.1s transform 0.2s;
	    background-color: white;
	    color: black;
	    transform: scale(1.08) translateY(2px);
	}
}

